var box: HTMLDivElement = document.createElement("div");
var boxTop: number;
var boxLeft: number;
var onoff: boolean;
var startX: number;
var startY: number;
box.className = "box";
document.body.appendChild(box);
box.style.width = "100px";
box.style.height = "100px";
box.style.border = "1px solid red";

document.body.onmousedown = (e: MouseEvent) => {
  onoff = true;
  startX = e.clientX;
  startY = e.clientY;
};

document.body.onmousemove = (e: MouseEvent) => {
  if (onoff === true) {
    boxTop = parseInt(box.style.top!) || 0;
    boxLeft = parseInt(box.style.left!) || 0;
    box.style.top = boxTop + e.clientY - startY + "px";
    box.style.left = boxLeft + e.clientX - startX + "px";
    startX = e.clientX;
    startY = e.clientY;
  }
};

document.body.onmouseup = (e: MouseEvent) => {
  onoff = false;
};
